/* ==========================================================================
  04. HEADER & NAVIGATION
  =========================================================================== */

#header-1.navik-header {
  padding-top: 40px;
}

#header-3.navik-header {
  padding-top: 20px;
}

/*------------------------------------------*/
/*  MAIN MENU LINK
/*------------------------------------------*/

.navik-menu ul li > a {
  display: block;
  font-size: 16px;
  font-weight: 500;
  text-transform: uppercase;
  -webkit-transition: all 150ms ease-in-out;
  -moz-transition: all 150ms ease-in-out;
  -o-transition: all 150ms ease-in-out;
  -ms-transition: all 150ms ease-in-out;
  transition: all 150ms ease-in-out;
}

#header-3 .navik-menu ul li > a {
  font-size: 18px;
  padding-left: 16px;
  padding-right: 16px;
}

.navbar-dark .navik-menu ul li > a {
  color: #212121;
}

.navbar-dark .navik-menu > ul > li.dropdown_menu > a > span::before {
  border-color: #212121;
}

.navik-menu ul li.dropdown_menu > a > span::before {
  top: -2px;
}

/*------------------------------------------*/
/*  Main Menu Link Hover
/*------------------------------------------*/

.navik-menu.navik-yellow ul li:hover > a,
.navik-menu.navik-yellow .mega-menu-heading a:hover {
  color: #f7be27;
}

.navik-menu.navik-meat ul li:hover > a,
.navik-menu.navik-meat .mega-menu-heading a:hover {
  color: #d2322b;
}

.navik-menu.navik-red ul li:hover > a,
.navik-menu.navik-red .mega-menu-heading a:hover {
  color: #e3000e;
}

.navik-menu.navik-salmon ul li:hover > a,
.navik-menu.navik-salmon .mega-menu-heading a:hover {
  color: #e84e1d;
}

/*------------------------------------------*/
/*  HEADER BUTTON
/*------------------------------------------*/

#header-3 .navik-menu ul li.nav-btn a {
  position: relative;
  top: 1.5px;
  font-size: 24px;
  font-weight: 700;
  letter-spacing: -0.5px;
  padding-left: 5px;
  padding-right: 5px;
}

#header-3 .navik-menu ul li.nav-btn.yellow-color > a {
  color: #f7be27;
}

#header-3 .navik-menu ul li.nav-btn.red-color > a {
  color: #e3000e;
}

#header-3 .navik-menu ul li.nav-btn.salmon-color > a {
  color: #e84e1d;
}

/*------------------------------------------*/
/*  SHOPPING BASKET
/*------------------------------------------*/

span.ico-holder {
  position: relative;
}

.basket-ico em.roundpoint {
  width: 20px;
  height: 20px;
  background-color: #e3000e;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  color: #fff;
  font-size: 13px;
  line-height: 18px;
  text-align: center;
  font-style: normal;
  position: absolute;
  top: -10px;
  right: -5px;
}

/*------------------------------------------*/
/*  HEADER CALL BUTTON
/*------------------------------------------*/

.callusbtn {
  display: none;
  position: absolute;
  top: 33px;
  left: 14px;
}

.callusbtn a {
  font-size: 22px;
  line-height: 1;
  padding: 0;
  margin: 0;
}

/*------------------------------------------*/
/*  HEADER SUBMENU
/*------------------------------------------*/

.navik-menu li > ul {
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  -o-border-radius: 2px;
}

.navik-menu ul ul li > a {
  font-size: 15px;
  font-weight: 500;
  font-style: normal;
  text-transform: inherit;
  padding: 14px 8px;
  text-align: right;
}

#header-3 .navik-menu ul ul li > a {
  font-size: 15px;
}

/*------------------------------------------*/
/*  NAVBAR STICKY
/*------------------------------------------*/

#header-1.navik-header.sticky,
#header-3.navik-header.sticky {
  padding-top: 0;
}

