  .cart-table table thead {
    border: none;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }

  .cart-table table tr {
    border: 1px solid #ddd;
    border-bottom: none;
    display: block;
    margin-bottom: 25px;
  }

  .cart-table table td {
    border-bottom: 1px solid #ddd;
    display: block;
    font-size: 1.25rem;
    text-align: right;
  }

  .cart-table table th,
  .cart-table table td,
  .cart-table table td.product-name {
    padding: 16px 30px;
  }

  .cart-product-price h5,
  .cart-product-total h5 {
    margin-top: 0;
  }

  .td-trash i {
    top: 0;
  }

  .cart-table table td::before {
    content: attr(data-label);
    float: right;
    font-weight: bold;
    margin-left: 30px;
  }

  .cart-table table td.product-name::before {
    position: relative;
    top: 15px;
